<tr>
<td>accessToken</td>
<td>String</td>
<td></td>
<td><code v-pre="">optional</code> 指定密钥。</td>
</tr>
<tr>
<td>server</td>
<td>String|Object</td>
<td></td>
<td><code v-pre="">optional</code> 指定 Cesium 在线服务器地址。</td>
</tr>
</tbody>
